<template>
  <div class="Search">
    <HeaderNavigation />
    <HeaderTitle />
    <div class="SearchBox">
      <div class="SearchBoxInput">
        <img src="@/assets/image/search.png" alt="" />
        <van-field v-model="value" :placeholder="placeholder" />
        <em @click="SearchClick()">搜尋</em>
      </div>
      <div class="SearchBoxTitle">
        希望淨水管家能夠幫助你
      </div>
    </div>
    <!-- 处理中弹窗 -->
    <van-popup v-model="inHandShow" class="vanPopupinHand">
      <img draggable="false" src="@/assets/image/inHand.png" alt="" />
      <h2>處理中，請稍候...</h2>
    </van-popup>
  </div>
</template>

<script>
import HeaderNavigation from "@/components/HeaderNavigation";
import HeaderTitle from "@/components/HeaderTitle";
export default {
  name: "Search",
  components: {
    HeaderNavigation,
    HeaderTitle
  },
  data() {
    return {
      inHandShow: false,
      value: "",
      placeholder: "請輸入商品兌換碼"
    };
  },
  mounted() {
    if (this.$route.query.router == "/InstallReplacement") {
      this.placeholder = "請輸入手機號碼"; // 请输入滤芯更换编号
    } else if (this.$route.query.router == "/CustomerService") {
      this.placeholder = "請輸入手機號碼"; // 請輸入預約服務
    } else if (this.$route.query.router == "/CustomerService/CustomerData") {
      this.placeholder = "請輸入手機號碼"; // 請輸入客戶資料
    } else if (
      this.$route.query.router == "/CustomerService/CommodityExchange"
    ) {
      this.placeholder = "請輸入商品兌換碼"; // 請輸入商品兌換碼
    } else if (
      this.$route.query.router == "/CustomerService/CommercialOpportunity"
    ) {
      this.placeholder = "請輸入手機號碼 "; // 請輸入商機通報
    }
  },
  methods: {
    SearchClick() {
      this.inHandShow = true;
      this.$router.push(this.$route.query.router + "?CustPhone=" + this.value);
      // var that = this;
      // // 定时两秒关闭加载中窗口
      // setTimeout(function() {
      //   that.inHandShow = false;
      //   that.$router.push(that.$route.query.router);
      // }, 2000);
    }
  }
};
</script>

<style lang="scss" scoped>
.Search {
  width: 100%;
  min-height: 1100vh;
  background: #ffffff;
  .SearchBox {
    padding: 30px 5.33% 0;
    .SearchBoxInput {
      padding: 0 16px;
      height: 50px;
      background: rgba(249, 249, 250, 1);
      border-radius: 12px;
      display: flex;
      align-items: center;
      .van-cell {
        width: 80%;
        font-size: 16px;
        font-weight: 500;
        color: rgba(189, 189, 189, 1);
        background: rgba(249, 249, 250, 1);
      }
      em {
        font-size: 16px;
        font-weight: 500;
        color: rgba(28, 118, 254, 1);
        line-height: 22px;
      }
    }
    .SearchBoxTitle {
      font-size: 14px;
      font-weight: 500;
      color: rgba(221, 221, 221, 1);
      line-height: 20px;
      text-align: center;
      margin-top: 64px;
    }
  }
  .vanPopupinHand {
    width: 206px;
    padding: 30px 0;
    height: 104px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 14px 60px 0px rgba(0, 0, 0, 0.05);
    border-radius: 15px;
    text-align: center;
    img {
      width: 60px;
      height: 60px;
    }
    h2 {
      font-size: 16px;
      font-weight: 500;
      color: rgba(134, 134, 134, 1);
      line-height: 20px;
      margin-top: 22px;
    }
  }
}
</style>
